"use client"

import { Moon, Sun } from "lucide-react"
import { useTheme } from "next-themes"
import { useEffect, useState } from "react"

export function ThemeToggle() {
  const [mounted, setMounted] = useState(false)
  const { theme, setTheme } = useTheme()

  useEffect(() => {
    setMounted(true)
  }, [])

  if (!mounted) {
    return (
      <button className="p-2 rounded-lg bg-muted">
        <Sun className="h-5 w-5" />
      </button>
    )
  }

  return (
    <button
      onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
      className="p-2 rounded-lg bg-muted hover:bg-muted/80 transition-all duration-300 hover:scale-110 active:scale-95 relative overflow-hidden"
      aria-label="Toggle theme"
    >
      <div className="relative w-5 h-5">
        {/* Soleil pour mode sombre */}
        <Sun className={`absolute inset-0 h-5 w-5 transition-all duration-500 ${
          theme === "dark" 
            ? "text-yellow-400 rotate-0 scale-100 opacity-100" 
            : "text-yellow-400 rotate-180 scale-0 opacity-0"
        }`} />
        
        {/* Lune pour mode clair */}
        <Moon className={`absolute inset-0 h-5 w-5 transition-all duration-500 ${
          theme === "light" 
            ? "text-blue-400 rotate-0 scale-100 opacity-100 animate-pulse" 
            : "text-blue-400 -rotate-12 scale-0 opacity-0"
        }`} />
      </div>
    </button>
  )
}
