"use client"

import { useRouter, usePathname } from "next/navigation"
import { useState } from "react"
import Image from "next/image"

const languages = [
  { code: 'fr', name: 'Fran√ßais', flag: 'üá´üá∑', image: '/flags/france.svg' },
  { code: 'en', name: 'English', flag: 'üá¨üáß', image: '/flags/uk.svg' },
  { code: 'es', name: 'Espa√±ol', flag: 'üá™üá∏', image: '/flags/spain.svg' },
]

export function LanguageSwitcher() {
  const [isOpen, setIsOpen] = useState(false)
  const router = useRouter()
  const pathname = usePathname()
  
  const currentLang = pathname.split('/')[1] || 'fr'
  const currentLanguage = languages.find(lang => lang.code === currentLang) || languages[0]

  const changeLanguage = (langCode: string) => {
    const segments = pathname.split('/').filter(Boolean)
    if (segments.length > 0 && ['fr', 'en', 'es'].includes(segments[0])) {
      segments[0] = langCode
    } else {
      segments.unshift(langCode)
    }
    router.push('/' + segments.join('/'))
    setIsOpen(false)
  }

  return (
    <div className="relative">
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="p-2 rounded-lg bg-muted hover:bg-muted/80 transition-all duration-300 hover:scale-110 active:scale-95"
        aria-label="Change language"
        title="S√©lectionner la langue"
      >
        <img 
          src={currentLanguage.image} 
          alt={currentLanguage.name}
          className="w-6 h-4 rounded transition-transform duration-300 hover:scale-125 object-cover"
        />
      </button>
      
      {isOpen && (
        <div className="absolute right-0 mt-2 w-56 bg-card border border-border rounded-lg shadow-xl overflow-hidden z-50 animate-in fade-in slide-in-from-top-2 duration-200">
          {languages.map((lang) => (
            <button
              key={lang.code}
              onClick={() => changeLanguage(lang.code)}
              className={`w-full px-4 py-3 text-left hover:bg-primary/10 transition-all duration-300 flex items-center gap-3 hover:translate-x-2 hover:text-primary ${
                lang.code === currentLang ? 'bg-primary/20 text-primary font-semibold' : 'text-foreground'
              }`}
              title={`Changer vers ${lang.name}`}
            >
              <img 
                src={lang.image}
                alt={lang.name}
                className="w-8 h-5 rounded transition-transform duration-300 hover:scale-150 object-cover"
              />
              <span>{lang.name}</span>
              {lang.code === currentLang && <span className="ml-auto text-primary">‚úì</span>}
            </button>
          ))}
        </div>
      )}
    </div>
  )
}
